body{
  min-height: 100vh;
  background-image: linear-gradient(to right, #434343 0%, black 100%);
}
.head{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  padding: 5px 10px;
  align-items: center;
  font-family: 'Bangers', cursive;
  color: #f85032 ;
font-size: 60px;
letter-spacing: 10px;

}

.head:hover{
  transform: scale(1.05);
 
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
 
}

.Search-bar
{
  width: 50%;
  border: none;
  padding: 15px 25px;
  border-radius: 10px 0px 0px 10px;
  font-family: 'Bangers', cursive;
  font-size: 15px;
}
.search-button
{
  border-radius: 0px 10px 10px 0px;
  justify-content: center;
  display: flex;
  align-items: center;
 background-image: linear-gradient(to right, #f85032  0%, #e73827 100%);
  color:white;
  border: none;
  padding: 15px 25px;
  font-family: 'Bangers', cursive;
  font-size: 15px;
}

.content
{
  
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  
  
}

.res{

  max-width: 300px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  /* grid-template-rows: 280px 80px 400px; */
  grid-template-areas: "image" "text" "stats";
  border-radius: 18px;
  background: white;
  box-shadow: 5px 5px 15px rgba(0,0,0,0.9);
  font-family: roboto;
  text-align: center;
  margin: 20px;
  
  
   /* border-radius: 10px;
  background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-width: 40%;
  box-shadow: 0px 5px 20px #e73827;  */
 

}

.res:hover{
  transform: scale(1.05);
  box-shadow: 5px 5px 15px rgba(0,0,0,0.6);
  /* margin: 100px; */
}

.res{
  transition: 0.5s ease;
  /* cursor: none; */
  margin:30px;
}

.image{
  grid-area: image;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  /* background-size: cover; */
}
.title {
  grid-area: text;
  margin-top:20px;
  text-align: center;
  font-family: 'Bangers', cursive;
 color: #e73827;
}
.list {
  grid-area: stats; 
  display: flex;
  
  flex-direction: column;
}




